import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { ViewVendorComponent } from './vendor/view-vendor/view-vendor.component';
import { AddEditVendorComponent } from './vendor/add-edit-vendor/add-edit-vendor.component';




const routes: Routes = [
  // {
  //   path:'',
  //   redirectTo: 'login'
  // },
  {
    path:'',
    component:LoginComponent
  },
  {
    path:'vendor',
    component:ViewVendorComponent
  },
  {
    path:'add_vendor',
    component:AddEditVendorComponent
  },
  {
    path:'edit_vendor/:id',
    component:AddEditVendorComponent
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
