import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from 'src/app/services/vendor.service';
import { Vendor } from 'src/app/vendor';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {

  constructor(private router: Router, private service: VendorService, private route: ActivatedRoute) { }

  // @Input() selectedStaff:Staff;
  vendors: Vendor[];
  vendor = new Vendor();
  searchText;
  searchid: any;
  view:any;

  ngOnInit() {
    this.getVendors();
    
    
  }
  add_vendor() {
    this.router.navigate(['add_vendor'])
  }
  edit_vendor(id: number) {
    this.router.navigate(['edit_vendor/'+id]);

  }
  logout(){
    this.router.navigate([''])
  }

  getVendors() {
    this.service.getAllVendors()
      .subscribe((data) => {
        this.vendors = data;
        console.log(data);
      }, (error) => {
        console.log(error);
      });
  }

  DisableVendor()
  {
    console.log("inside disable" +this.searchid);
    this.service.disableVendor(this.searchid)
    .subscribe((data)=>{
      console.log(data);
      this.getVendors();
    },(error)=>{
      console.log(error);
    });   
  }
  onSelect(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendor_id);
    this.searchid = selectedVendor.vendor_id;
  }
  onSelect1(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendor_id);
    this.view = selectedVendor.vendor_id;
    this.viewContact;
    this.viewContact(this.view);
  }
  viewContact(data){
    this.router.navigate(['view-vendor',this.view])

  }

}
