import { Component, OnInit } from '@angular/core';
import { VendorService } from 'src/app/services/vendor.service';
import { Router, ActivatedRoute } from '@angular/router';

import { Vendor } from 'src/app/vendor';

@Component({
  selector: 'app-add-edit-vendor',
  templateUrl: './add-edit-vendor.component.html',
  styleUrls: ['./add-edit-vendor.component.scss']
})
export class AddEditVendorComponent implements OnInit {

  vendor=new Vendor();
  constructor(private router: Router,private route:ActivatedRoute,private _service: VendorService) { }

  ngOnInit() {
    this.route.params.subscribe(params=>this.getVendorById(params['id']));
    
  }
  

  


  add_vendor() {
    // this.getVendorById();
    this._service.insertvendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }
name:string;
  getVendorById(vendor_id:number){
    this._service.getVendor(vendor_id)
    .subscribe((vendorData)=>{
      this.vendor=vendorData;
     
      console.log(this.vendor);
      console.log(this.vendor.vendor_name)
    },(error)=>{
      console.log(error);
    });
  }
  back(){
    this.router.navigate(['vendor'])
  }
  logout(){
    this.router.navigate([''])
  }
  

}
